:root {
  --navy:#272996b0;
  
}

body {
  background-image: linear-gradient(45deg, rgba(173, 216, 230, 0.651), rgba(86, 253, 217, 0.733));
  justify-content: center;
  margin: auto;
  font-family: 'Open Sans', sans-serif; 
}

#nav, #upArrowArea {
  text-align: center;
}

button {
  border: none;
  background-color: transparent;
}

#homeBtn, #email, a {
  color: #ffffff;
  text-decoration: none;
}

i {
  color: var(--navy);
  /* margin-right: 15px; */

}

i:hover {
  opacity: 0.5;
}
.upArrowIcon {
  margin: 1%;
  padding: 1%;
  opacity: 1.0;
}


#mainArea {
  background-color: var(--navy);
  color: #ffffff;
  border-radius: 5px;
}

#profilePic {
  max-width: 103%;
  border-radius: 5px;
}


.projects {
  width: 100%;
  margin-top: 10%;
}

#burger {
  width: 50%;
}
